<template>
  <el-dialog :visible.sync="visible" :before-close="closeDialog" width="40%">
    <div slot="title">{{dialogTitle}}</div>
    <div class="body">
      <el-row>
        <el-col :span="4">
          <span class="row_title"></span>
        </el-col>
        <el-col :span="20">
          <el-input placeholder="" v-model="tempCourse.name"></el-input>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="4">
          <span class="row_title"></span>
        </el-col>
        <el-col :span="20">
          <el-input placeholder="()" v-model="tempCourse.className"></el-input>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="4">
          <span class="row_title"></span>
        </el-col>
        <el-col :span="10">
          <el-select v-model="tempCourse.schoolYear">
            <el-option value="2010-2011" label="2010-2011"></el-option>
            <el-option value="2011-2012" label="2011-2012"></el-option>
            <el-option value="2012-2013" label="2012-2013"></el-option>
            <el-option value="2013-2014" label="2013-2014"></el-option>
            <el-option value="2014-2015" label="2014-2015"></el-option>
            <el-option value="2015-2016" label="2015-2016"></el-option>
            <el-option value="2016-2017" label="2016-2017"></el-option>
            <el-option value="2017-2018" label="2017-2018"></el-option>
            <el-option value="2018-2019" label="2018-2019"></el-option>
            <el-option value="2019-2020" label="2019-2020"></el-option>
          </el-select>
        </el-col>
        <el-col :span="10">
          <el-select v-model="tempCourse.semester">
            <el-option value="0" label=""></el-option>
            <el-option value="1" label=""></el-option>
            <el-option value="2" label=""></el-option>
          </el-select>
        </el-col>
      </el-row>
      <el-row>
        <div class="addition_text"></div>
        <el-checkbox label=""></el-checkbox>
        <el-checkbox label=""></el-checkbox>
        <el-checkbox label=""></el-checkbox>
      </el-row>
    </div>
    <div slot="footer">
      <el-button @click="closeDialog" type="info"></el-button>
      <el-button @click="submit" type="primary"></el-button>
    </div>
  </el-dialog>
</template>
<script>
export default {
  data() {
    return {};
  },
  props: {
    dialogType: {
      required: true
    },
    visible: {
      required: true,
      type: Boolean
    },
    course: {}
  },
  computed: {
    dialogTitle: {
      get() {
        var title = "";
        if ("create" === this.dialogType) {
          title = "";
        } else if ("update" === this.dialogType) {
          title = "";
        }
        return title;
      }
    },
    tempCourse:{
      get(){
        return new Object(this.course);
      }
    }
  },
  methods: {
    closeDialog() {
      this.visibleChanged();
    },
    visibleChanged() {
      this.$emit("visibleChanged", !this.visible);
    },
    createCourse(){
      this.$axios
        .post("api/course/addCourse",this.course)
        .then(res => {
          this.$message({
            message: "",
            type: "success"
          });
          this.closeDialog();
        })
        .catch(err => {
          this.$message({
            message: "",
            type: "error"
          });
        });
    },
    updateCourse(){
      this.$axios
        .post("api/course/updateCourse",this.course)
        .then(res => {
          this.$message({
            message: "",
            type: "success"
          });
          this.closeDialog();
        })
        .catch(err => {
          this.$message({
            message: "",
            type: "error"
          });
        });
    },
    submit() {
      if('create'===this.dialogType){
        this.createCourse();
      }
      else if('update'===this.dialogType){
        this.updateCourse();
      }
    }
  },
};
</script>
<style scoped>
.row_title {
  position: relative;
  top: 10px;
  font-size: 17px;
}
.el-row {
  margin-bottom: 10px;
}
.addition_text {
  font-size: 13px;
  margin-top: 15px;
  margin-bottom: 15px;
}
</style>


