<template>
  <el-form class="register_form" label-width="100px">
    <el-form-item label="">
      <el-select v-model="user.role">
        <el-option value="student">student</el-option>
        <el-option value="teacher">teacher</el-option>
      </el-select>
    </el-form-item>
    <el-form-item label="/" placeholder="">
      <el-input v-model="user.id"></el-input>
    </el-form-item>
    <el-form-item label="" placeholder="">
      <el-input v-model="user.name"></el-input>
    </el-form-item>
    <el-form-item label="">
      <el-input v-model="user.password" placeholder="6~16 ">
      </el-input>
    </el-form-item>
    <el-form-item label="">
      <el-input v-model="user.realName" placeholder=""></el-input>
    </el-form-item>
    <el-form-item label="">
      <el-input
        v-model="user.phone"
        placeholder=""
        show-word-limit
        maxlength="11"
      ></el-input>
    </el-form-item>
    <el-form-item label="">
      <el-input v-model="user.age" placeholder=""></el-input>
    </el-form-item>
    <el-form-item label="">
      <el-select v-model="user.sex">
        <el-option value=""></el-option>
        <el-option value=""></el-option>
      </el-select>
    </el-form-item>
    <el-form-item>
      <el-button type="primary" @click="submit" v-show="option != 'read'"
        ></el-button
      >
      <el-button type="danger" @click="cancel"></el-button>
    </el-form-item>
  </el-form>
</template>

<script>
export default {
  data() {
    return {
      user: {
        name: "",
        password: "",
        role: "",
        id: "",
        realName: "",
        phone: "",
        age: "",
        sex: "",
      },
      option: "add",
    };
  },
  methods: {
    submit() {
      if ("add" === this.option) {
        this.registerUser();
      } else if ("edit" === this.option) {
        this.updateUser();
      }
    },
    cancel() {
      this.$router.go(-1);
    },
    registerUser() {
      this.$axios
        .post("/api/register/registerUser", this.user)
        .then((res) => {
          this.$message.success("");
          this.$router.go(-1);
        })
        .catch((err) => {
          this.$message.error("");
          console.log(err);
        });
    },
    updateUser() {
      this.$axios
        .post("/api/user/updateUser", this.user)
        .then((res) => {
          this.$message.success("");
          this.$message.success("");
          this.$router.go(-1);
        })
        .catch((err) => {
          this.$message.error("");
          console.log(err);
        });
    },
    getUserById(id) {
      this.$axios
        .get("/api/user/getUserById?id=" + id)
        .then((res) => {
          this.user = res.data;
        })
        .catch((err) => {
          this.$message.success("");
          console.log(err);
        });
    },
  },
  mounted() {
    this.option = this.$route.params.option;
    if ("read" === this.option) {
      var id = this.$route.params.id;
      this.getUserById(id);
    } else if ("edit" === this.option) {
      id = this.$route.params.id;
      this.getUserById(id);
    } else {
      this.option="add"
    }
  },
};
</script>

<style scoped>
.register_form {
  width: 300px;
  margin: 0 auto;
}
.input {
  width: auto;
}
</style>


